import * as types from './mutation-types'

export default{
  //
  signIn({ commit, state },obj){
      var me=obj['context'];
      let _emailPhone=state.form.emailPhone;
      let _password=state.form.password;
      var item={
        ep:_emailPhone,
        pw:_password
      }
      //vue-sourceshttp serverconfig/index.jsproxyTable
      me.$http.post("/users/user",item).then(
          function (res) {
              // 
              if (res.data.success) {
                location.href='#/home'
                // 
                me.$emit('upup',res.data.userInfo);
                return;
              }
              else{
                alert(res.data.error);
              }
              console.log(res.data);
          },function (res) {
              // 
              console.log(res);
          }
      );
      // for (var i = 0,len=state.userList.length; i < len; i++) {
      //     if (state.userList[i].emailPhone===_emailPhone&&state.userList[i].password===_password) {
      //         location.href='#/home'
      //         // 
      //         me.$emit('upup',state.userList[i]);
      //         return;
      //     }
      // }
      // alert("");
  },
  //
  signUp({ commit, state },obj){
    var me=obj['context'];
    let _name=state.form.name;
    let _emailPhone=state.form.emailPhone;
    let _password=state.form.password;
        var item={
      n:_name,
      ep:_emailPhone,
      pw:_password
    };
    // post
    me.$http.post("/users/insert",item).then(
          function (res) {
              // 
              if (res.data.success) {
                alert("");
                return;
              }
              else{
                alert(res.data.error);
              }
              console.log(res.data);
          },function (res) {
              // 
              console.log(res);
          }
      );
    // commit(types.pushUserList,{
    //     name:_name,
    //     emailPhone:_emailPhone,
    //     password:_password
    // });
    // alert("");
  },
  //
  changeSignInfo({ commit, state },value){
    commit(types.changeSigninfo,value);
  }
}



